import type { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';
import { TP_ICONS_STORYBOOK } from '../../../../../utils/constants/icon.constant';
import { TpAddCard } from '../add-card';
import { TpAddCardType } from '../utils/add-card.type';

const meta: Meta<TpAddCard & TpAddCardType> = {
  title: 'components/Core/Task/Add card',
  argTypes: {
    iconName: {
      control: { type: 'select' },
      options: [...TP_ICONS_STORYBOOK],
      description: 'Define o path e nome do icone.',
    },
    titulo: {
      control: { type: 'text' },
      description: 'Define o texto do componente.',
      table: {
        defaultValue: {
          summary: '',
        },
        type: {
          summary: 'text',
        },
      },
    },
  },
  args: {
    titulo: 'Adicionar tarefa',
    iconName: `${TP_ICONS_STORYBOOK[1]}`,
  },
};

export default meta;

type Story = StoryObj<TpAddCard & TpAddCardType>;

export const Default: Story = {
  render: ({ ...args }) => {
    const iconUrl = `./assets/${args.iconName}`;

    return html`
      <tp-add-card
        color=${args.color}
        icon-name=${iconUrl}
        titulo=${args.titulo}
      ></tp-add-card>
    `;
  },
};
